 /* Reset styles */
 * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Poppins', sans-serif;
    background-color: #f9f9f9;
    color: #333;
}

/* Top navigation bar */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #002147;
    padding: 10px 20px;
    color: white;
}

.navbar .logo {
    font-size: 1.5em;
    font-weight: bold;
}

.navbar ul {
    list-style: none;
    display: flex;
    gap: 20px;
}

.navbar ul li {
    display: inline-block;
}

.navbar ul li a {
    color: white;
    text-decoration: none;
}

.navbar-right {
    display: flex;
    align-items: center;
    gap: 40px; 
}

.navbar-right a {
    color: white;
    text-decoration: none;
}

.navbar-right .login, .navbar-right .register {
    font-weight: bold;
}

.search-bar {
    background-color: #fff;
    padding: 5px 10px;
    border-radius: 5px;
    border: none;
}

.search-bar input {
    border: none;
    outline: none;
    padding: 5px;
}

.search-bar button {
    background-color: #f39c12;
    border: none;
    padding: 9px 10px;
    cursor: pointer;
    color: white;
}

/* Hero section */
.hero {
    display: flex;
    justify-content: center;
    align-items: center;
    background: url('../images/living1.jpg') center/cover no-repeat;
    height: 90vh;
    color: white;
    position: relative;
}

.hero-content {
    text-align: center;
    z-index: 2;
}

.hero-content h1 {
    font-size: 3em;
    margin-bottom: 20px;
}

.hero-content p {
    font-size: 1.2em;
    margin-bottom: 30px;
}

.hero-content .btn {
    background-color: #f39c12;
    padding: 15px 30px;
    border-radius: 5px;
    text-decoration: none;
    color: white;
    font-weight: bold;
    margin: 0 10px;
    transition: background-color 0.3s;
}

.hero-content .btn:hover {
    background-color: #e67e22;
}

.hero-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.4);
}

/* Modal Styles */
#loginModal, #registerModal {
    display: none;
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    justify-content: center;
    align-items: center;
}

.modal-content {
    background-color: white;
    padding: 30px;
    border-radius: 15px;
    width: 400px;
    text-align: center;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
}

.modal.show {
    display: block; /* Make modal visible */
  }

.modal-content h2 {
    margin-bottom: 20px;
    color: #002147;
}

.input-group {
    position: relative;
    margin-bottom: 20px;
}

.modal-content input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
}

.modal-content button {
    width: 100%;
    padding: 10px;
    background-color: #f39c12;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.modal-content button:hover {
    background-color: #e67e22;
}

.close-modal {
    position: absolute;
    top: 10px;
    right: 10px;
    color: #999;
    font-size: 20px;
    cursor: pointer;
}

.modal-footer {
    margin-top: 15px;
    font-size: 14px;
}

.modal-footer a {
    color: #f39c12;
    text-decoration: none;
}

/* Error message styles */
.error-message {
    color: red;
    font-size: 14px;
    font-style: italic;
    margin-top: 5px;
    text-align: left;
}

/* Responsive Styles */
@media (max-width: 768px) {
    .navbar ul {
        flex-direction: column;
        gap: 10px;
    }

    .navbar-right {
        flex-direction: column;
        gap: 20px;
    }

    .hero-content h1 {
        font-size: 2.5em;
    }

    .hero-content p {
        font-size: 1em;
    }

    .modal-content {
        width: 90%;
    }
}

@media (max-width: 480px) {
    .navbar {
        flex-direction: column;
        align-items: flex-start;
    }

    .hero-content h1 {
        font-size: 1.8em;
    }

    .hero-content p {
        font-size: 0.9em;
    }

    .btn {
        padding: 10px 20px;
        font-size: 0.9em;
    }

    .modal-content {
        width: 95%;
    }
}